import Vue from 'vue'
import Router from 'vue-router'
import Home from './views/Home'
import Wedo from './views/wedo'
import Services from './views/servicios'
import Howwedo from './views/howwedo'

import About from './views/About'

Vue.use(Router)

export default new Router({
  // mode:'history',
  routes: [
    {
      path: '*',
      redirect: '/inflight/home'
    },
    {
      path: '/',
      redirect: '/inflight/home'
    },
    {
      path: '/inflight/home',
      name: 'home',
      component: Home
    },
    {
      path: '/inflight/wedo',
      name: 'wedo',
      component: Wedo
    },
    {
      path: '/inflight/howwedo',
      name: 'howwedo',
      component: Howwedo
    },
    {
      path: '/inflight/servicios',
      name: 'servicios',
      component: Services
    },
    {
      path: '/inflight/about',
      name: 'about',
      component: About
    }
  ]
})
